@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 41px 0 20px;
    }
	
	.mkdf-comment {
		
		&.mkdf-pingback-comment {
			
			.mkdf-comment-text {
				padding: 0;
			}
		}
	}

    .mkdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 0;
            padding: 3px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 40px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 40px 99px;
        min-height: 82px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 0 0 13px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            @include mkdfMetaStyle($font-size:12px);
			letter-spacing: .1em;
            margin-right: 15px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        .mkdf-comment-name {
            margin: 0;
			max-width: 80%;
        }

        .mkdf-comment-date {
            display: inline-block;
            margin: 0 0 0 3px;
			font-family: $default-nav-font;
            font-size: 10px;
			font-weight: 500;
			line-height: 2em;
            color: $meta-color;
			letter-spacing: .15em;
			text-transform: uppercase;
        }

        .mkdf-text-holder p {
            margin: 0 0 12px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 28px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 25px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .mkdf-comment-form-inner {
        padding: 2px 0 21px;
    }
    
    .comment-reply-title {
        margin: 0 0 12px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {
	        
            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
	&.wpcf7-checkbox{
		 .wpcf7-list-item{
			 display: block;
			 margin: 4px 0 8px;
			 &.last{
				 margin-bottom: 24px;
			 }
		 }
	 }
	&.wpcf7-textarea{
		max-width: 100%;
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include mkdfButtonDefaultStyle();
	@include mkdfButtonMediumParams();
	@include mkdfButtonSolidColor();
	cursor: pointer;
	
	&:hover {
		@include mkdfButtonSolidHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}
p.form-submit{
  margin-top: 6px;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
	height: 150px;
}

.wpcf7-form {

	.mkdf-two-columns-form-without-space .ajax-loader {
		display: none;
	}
}

span.wpcf7-form-control-wrap.mkdf-select:after {
    font-family: FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    content: '\f107';
    right: 20px;
    top: 0px;
    padding: 0 0 2px;
    position: absolute;
    pointer-events: none;
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.mkdf-slider-is-moving {
		
		.owl-item {
			
			.mkdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.mkdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }

            .mkdf-ps-fullscreen & {
                position: absolute;
                top:0;
                width: 100%;
                height: 100%;

                article {
                    width:100% !important;
                }

                .owl-prev,
                .owl-next {
                    @include mkdfTransform(translateY(-50%));
                }
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: #b5b0b0;
			@include mkdfTransform(translateY(-50%));
			@include mkdfTransition(color .2s ease-out);
			
			&:hover {
				color: $default-heading-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 30px;
		}
		
		.owl-next {
			right: 30px;
		}
	}
	
	.owl-dots {
		text-align: center;
        padding: 66px 0 0;
		
        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            position: relative;
            line-height: 32px;

			.mkdf-owl-dot-num {
				position: absolute;
				top:-1em;
				width:100%;
				text-align: right;
				color: #cfcccc;
				font-size: 19px;
				font-family: $default-heading-font;
				font-weight: 400;

				&.mkdf-first-dot {
					text-align: left;
					color: $first-main-menu-color;
				}
            }

            span {
                display: inline-block;
                vertical-align: middle;
                width: 55px;
                height: 2px;
                margin: 0;
                background-color: #cfcccc;
                box-sizing: border-box;
                @include mkdfTransition(background-color .2s ease-out, border .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    background-color: $first-main-menu-color;
                    border-color: $first-main-menu-color;
                }

                & ~ * span {
                  background-color: #cfcccc;
                }
            }


        }
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            img {
                border: 2px solid transparent;
                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .mkdf-wrapper {
        padding: 0 !important;
    }
    
    .mkdf-content {
        height: 100vh;
    }
	
	&.admin-bar {
		
		.mkdf-content {
			height: calc(100vh - 32px);
		}
	}

    .mkdf-content-inner {
	    @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
	    @include mkdfTableCellLayout();
        padding: 0 20%;
	    text-align: center;

		.mkdf-404-form{
			&.mkdf-404-form-light{
				.input-holder{
					border-color: #fff;

					input,
					button{
						color: #fff;
					}

					input.search-field {

						@include mkdfPlaceholder {
							color: #fff;
						}
					}

					button:hover{
						color: rgba(255,255,255,0.8);
					}
				}
			}

			.input-holder {
				width: 45%;
				min-width: 300px;
				border: 1px solid $default-border-color;
				margin: 30px auto 0;

				input,
				button {
					background-color: transparent;
					border: 0;
					border-radius: 0;
					box-sizing: border-box;
					color: $default-form-color;
					display: inline-block;
					float: left;
					font-family: $default-text-font;
					font-size: 14px;
					font-style: italic;
					font-weight: inherit;
					height: 48px;
					line-height: 46px;
					outline: 0;
					position: relative;
					vertical-align: top;
					-webkit-appearance: none;

					@include mkdfTransition(color .2s ease-in-out);
				}

				input.search-field {
					width: calc(100% - 48px);
					padding: 10px 0 10px 21px;

					@include mkdfPlaceholder {
						color: $default-form-color;
					}
				}

				button {
					background: transparent;
					color: $default-heading-color;
					cursor: pointer;
					font-size: 17px;
					width: 48px;

					@include mkdfTransition(color .2s ease-in-out);

					&:hover {
						color: #000;
					}

					span {
						display: block;
						left: 0;
						line-height: 48px;
						position: absolute;
						top: 0;
						width: 100%;

						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
		}
    }

	@include ipad-landscape {

		.mkdf-content {
			height: auto;
			min-height: 100vh;

			.mkdf-content-inner {
				min-height: 100vh;
			}
		}

		&.admin-bar {

			.mkdf-content {
				height: auto;
				min-height: calc(100vh - 32px);
			}
		}
	}

    @include ipad-portrait {

        .mkdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include mkdfTransition(opacity 0.3s ease);
    }

    > span {
		position: relative;
		display: block;
		width: 39px;
		height: 39px;
		line-height: 40px;
		font-size: 15px;
		text-align: center;
		color: #fff;
		background-color: $first-main-color;
		border: 1px solid $first-main-color;
		overflow: hidden;
		@include mkdfDefaultTransition(color, background-color, border-color);


		i:first-child {
			@include mkdfTransform(translateY(0));
		}

		i:last-child {
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			height: 100%;
			line-height: inherit;
			@include mkdfTransform(translateY(0));
		}

        i {
            @include mkdfTransition(.25s ease .05s);
        }

        &:hover {

            i:first-child {
                @include mkdfTransform(translateY(-300%));
            }

            i:last-child {
                @include mkdfTransform(translateY(-100%));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
	
    .mkdf-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */
// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	display: none;
}

// IE
input[type="search"]::-ms-clear {
	display: none;
}

input[type=text]::-ms-clear {
	display: none;
}